      to discover the issues they’ve told us matter the most to them.
      <b>Type in your location to see the stories nearest your area or click a pin on the map to find out more.</b>
    </p>
    <div id="map" class="map">
      <div id="legend">
        <svg width="32px" fill="#000000" version="1.1" x="0px" y="0px" viewBox="0 0 32 50.036" enable-background="new 0 0 100 100" xmlns="http://www.w3.org/2000/svg" xmlns:bx="https://boxy-svg.com">
          <circle style="fill: rgb(255, 255, 255);" cx="16" cy="21" r="9.625" />
          <g transform="matrix(0.440792, 0, 0, 0.440792, -6.039605, 4.79604)">
            <path
              d="M50,5C32.459,5,18.239,19.22,18.239,36.761C18.239,54.302,50,95,50,95s31.761-40.698,31.761-58.239   C81.761,19.22,67.541,5,50,5z M50.183,56.702c-11.206,0-20.29-9.084-20.29-20.29c0-11.205,9.084-20.289,20.29-20.289   c11.206,0,20.289,9.084,20.289,20.289C70.473,47.618,61.389,56.702,50.183,56.702z"
              style="fill: rgb(245, 219, 55);" />
          </g>
          <text x="15.993" y="26.593" style="white-space: pre; fill: rgb(51, 51, 51); font-family: Oswald; font-size: 14.3px; text-anchor: middle;"></text>
        </svg>

        number of girls stories
      </div>
    </div>

    <div class="search-form">
      <div class="search">

        <!-- <autocomplete :items="locations" :maxSuggestions="5" :placeholderText="'find a story'" @input="onLocationChange"></autocomplete> -->
        <dropdown :title="'Filter stories by location'" :items="locations" @change="onLocationChange"></dropdown>

      </div>
      <!-- <button class="btn btn-orange btn-search" @click="filter">
        Search
      </button> -->
    </div>
    <p class="explain">Text to explain search functionality goes here.</p>
    <tags-component v-bind:tags="availableTags" @input="onTagsChange" @reset="resetFilter" :values="tags"></tags-component>

  </section>

  <popup v-body-scroll-lock="itemsToShowInPopUp.length > 0" v-if="itemsToShowInPopUp.length > 0" :items="itemsToShowInPopUp" :geoJson="geoJson" :center="popupMapCenter" @close="itemsToShowInPopUp = []" @clickOnTag="onTagsChange">
  </popup>
</div>
</template>

<script>
import * as L from 'leaflet'
import {
  MarkerClusterGroup
} from 'leaflet.markercluster'
import * as Papa from 'papaparse'
import Tags from './components/Tags.vue'
import Popup from './components/Popup.vue'
import Dropdown from './components/Dropdown.vue'

export default {
  name: 'app',
  components: {
    'popup': Popup,
    'tags-component': Tags,
    'dropdown': Dropdown
  },
  data() {
    return {
      config: undefined,
      geoJson: undefined,
      map: null,
      tags: [],
      items: [],
      itemsToShow: [],
      itemsToShowInPopUp: [],
      popupMapCenter: undefined,
      markerClusterGroup: undefined,
      showModalMap: false,
      autocompleteResults: []
    }
  },
  watch: {

    itemsToShow: function() {
      // trigger markers redraw
      console.log(this.itemsToShow);
      this.showMarkers();
    }
  },
  computed: {
    zoomLevel() {
      return window.innerWidth < 768 ? 4.8 : 5.8;
    },
    availableTags() {
      var tags = [];

      if (this.items.length > 0) {

        this.items.forEach(item => {
          if (item && item.themeTags) {

            var themeTags = item.themeTags.split(',');

            themeTags.forEach(tag => {
              tag = tag.trim().toLowerCase()
              if (tags.indexOf(tag) < 0) {
                tags.push(tag);
              }
            });
          }

        });

      }
      return tags;

    },
    locations() {
      var locations = [];

      if (this.items.length > 0) {
        this.items.forEach(item => {
          if (locations.indexOf(item.location) < 0) {
            locations.push(item.location);
          }
        });

      }
      return locations;
    },


  },
  mounted() {

    this.config = window._appConfig;
    if (this.config) {
      this.init();
    }

  },
  methods: {
    onLocationChange(location) {
      this.location = location;
      this.filter();
    },
    onTagsChange(tags) {
      if (this.itemsToShowInPopUp.length > 0) {
        this.itemsToShowInPopUp = [];
      }

      this.tags = tags;
      this.filter();
    },
    filter() {

      this.itemsToShow = this.items.filter(item => {

        var trigger = true;
        // Filter by Location

        if (this.location && this.location.length) {
          if (item.location && item.location == this.location) {
            // Filer by tags
            trigger = true;
          } else {
            trigger = false;
          }
        } else {
          trigger = true;
        }

        return trigger && this.filterItemByTags(item);
      });


    },
    filterItemByTags(item) {

      if (this.tags && this.tags.length > 0) {

        var trigger = false,
          themeTags = item.themeTags.split(',');

        themeTags.forEach(tag => {
          tag = tag.trim().toLowerCase();
          if (this.tags.indexOf(tag) >= 0) {
            trigger = true;
          }
        });

        return trigger;
      }

      return true;
    },
    resetFilter() {
      this.tags = [];
      this.location = undefined;
      this.$children[0].setIndex(-1);

    },
    showMarkers() {

      if (!this.map) {
        return false;
      }




      if (this.markerClusterGroup) {
        this.map.removeLayer(this.markerClusterGroup);
        this.markerClusterGroup = undefined;
      }


      if (this.itemsToShow.length > 0) {
        this.markerClusterGroup = L.markerClusterGroup({
          iconCreateFunction: function(cluster) {
            return L.divIcon({

              // html: '<div>' + cluster.getChildCount() + '</div>',
              html: '<svg width="32px" fill="#000000" version="1.1" x="0px" y="0px" viewBox="0 0 32 50.036" enable-background="new 0 0 100 100" xmlns="http://www.w3.org/2000/svg" xmlns:bx="https://boxy-svg.com"><circle style="fill: rgb(255, 255, 255);" cx="16" cy="21" r="9.625"/><g transform="matrix(0.440792, 0, 0, 0.440792, -6.039605, 4.79604)"><path d="M50,5C32.459,5,18.239,19.22,18.239,36.761C18.239,54.302,50,95,50,95s31.761-40.698,31.761-58.239   C81.761,19.22,67.541,5,50,5z M50.183,56.702c-11.206,0-20.29-9.084-20.29-20.29c0-11.205,9.084-20.289,20.29-20.289   c11.206,0,20.289,9.084,20.289,20.289C70.473,47.618,61.389,56.702,50.183,56.702z" style="fill: rgb(245, 219, 55);"/></g><text x="15.993" y="26.593" style="white-space: pre; fill: rgb(51, 51, 51); font-size: 14.3px; text-anchor: middle;">' +
                cluster.getChildCount() + '</text></svg>',
              iconSize: new L.Point(32, 50)
            });
          },
          singleMarkerMode: true,
          spiderfyOnMaxZoom: false,
          showCoverageOnHover: false,
          zoomToBoundsOnClick: false
        });

        this.itemsToShow.forEach(item => {
          var marker = L.marker([item.coordinates.lat, item.coordinates.lng], {
            index: item.index
          }).on('click', (e) => {
            if (this.items[e.sourceTarget.options.index]) {
              this.itemsToShowInPopUp = [
                this.items[e.sourceTarget.options.index]
              ];
              var coordinates = this.items[e.sourceTarget.options.index].coordinates;
              this.popupMapCenter = [coordinates.lat, coordinates.lng];
            } else {
              console.log("wrong index");
            }
          });


          this.markerClusterGroup.addLayer(marker);
        });


        // click on cluster
        this.markerClusterGroup.on('clusterclick', (a) => {
          // get all markers in cluster


          var markersInCluster = a.layer.getAllChildMarkers(),
            itemsToShowInPopUp = [];

          markersInCluster.forEach(marker => {

            if (this.items[marker.options.index]) {
              itemsToShowInPopUp.push(this.items[marker.options.index]);
            } else {
              console.log("wrong index");
            }
          });

          this.itemsToShowInPopUp = this.shuffle(itemsToShowInPopUp);
          this.popupMapCenter = [a.latlng.lat, a.latlng.lng];


        });

        this.map.addLayer(this.markerClusterGroup);


      }
    },
    init() {
      this.get(this.config.geoJSONPath, (response) => {

        this.geoJson = this.validateJsonString(response);

        if (this.geoJson) {
          this.initMap();
          this.loadData();
          this.showMarkers();
        }
      });


    },
    loadData() {


      this.get(this.config.CSVPath, (response) => {

        var results = Papa.parse(response),
          items = [],
          item;

        if (results) {

          for (var i = 1; i < results.data.length; i++) {

            item = results.data[i];
            var lat = parseFloat(+item[4]),
              lng = parseFloat(+item[5]);

            if (!isNaN(lat) && !isNaN(lng)) {

              items.push({
                "index": i - 1,
                "name": item[0],
                "age": item[1],
                "location": item[2],
                "region": item[3],
                "coordinates": {
                  "lat": parseFloat(+item[4]),
                  "lng": parseFloat(+item[5])
                },
                "themeTags": item[6],
                "photoURL": item[7],
                "youtubeURL": item[8],
                "text": item[9],
                "callToAction": {
                  "url": item[10],
                  "text": item[11]
                }
              });
            }

          }

          this.items = items;
          this.itemsToShow = this.items;
        }
      });
    },

    initMap() {

      this.map = L.map('map', {
        zoomLevel: this.zoomLevel,
        maxZoom: this.zoomLevel,
        continuousWorld: true,
        zoomControl: false,
        dragging: false,
        touchZoom: false,
        doubleClickZoom: false,
        boxZoom: false,
        tap: false,
        scrollWheelZoom: false,
        static: true
      }).setView([54.904571379248125, -3.436872187499991], this.zoomLevel);



      var geoJsonLayer = L.geoJson(this.geoJson, {
        clickable: true,
      }).addTo(this.map);


    },

    shuffle(array) {
      var currentIndex = array.length,
        temporaryValue, randomIndex;

      // While there remain elements to shuffle...
      while (0 !== currentIndex) {

        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;

        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
      }

      return array;
    },
    get(url, callback) {
      var xhr = new XMLHttpRequest();

      xhr.onreadystatechange = function() {
        if (xhr.readyState === XMLHttpRequest.DONE && xhr.status === 200) {
          callback(xhr.responseText);
        }
      };

      xhr.open("GET", url, true);
      xhr.send();
    },
    validateJsonString(str) {
      var json = false;

      try {
        json = JSON.parse(str);
      } catch (e) {
        return false;
      }

      return json;
    }
  }
}
</script>

